module.exports = `Smart Player是一个完全去中心化的金融游戏，存在不确定的风险因素，请您熟知以下规则后理性参与：
一、Smart Player风险准则：
 1、不准向他人做任何形式的承诺和保障；
 2、不准夸大事实向他人宣传；
 3、不准收取任何会员的资金；
 4、不准用任何特殊协议吸引他人；
 5、充分了解以后方可与他人分享；
 6、不能破坏以上任何条例。
二、Smart Player玩法规则：
 1、分红收益：
  15天：0.7%/天
  30天：1%/天
  60天：1.5%/天
  90天：2%/天
 2、收益：静态收益每天到账，可以单次领取，也可以累计多次再领取，玩家自由决定，无取款限制！
 3、复投：已参与的玩家，可以在48小时后参与复投，复投将更新合约冻结周期；
 4、合约到期：合约到期，可以继续参与，也可以赎回本金；赎回本金，将扣除20%的手续费；
 5、分享收益：
  直推1人拿1代
  直推2人拿2代
  直推3人拿3代
  ...........
  直推6人拿15代
  1代提现收益的30%
  2代提现收益的20%
  3代提现收益的10%
  4-13代提现收益的5%
  14代提现收益的10%
  15代提现收益的20%
 6、烧伤机制：参与10ETH以下，分享收益以自身投资金额1：1烧伤，参与10ETH以上，无烧伤；
 7、幸运池：每日参与资金的4%进入幸运池，平均奖励给从每日累计参与满20ETH的玩家中随机抽出的5名幸运者；
 8、全球股东分红：每日参与资金的8%进入全球股东分红池，其中V1、V2、V3、V4股东池各分配25%，V4级别的玩家可以获得V1-V4所有股东池的加权分红，V3级别可以分到V1-V3股东池的加权分红，V1级别只能分到V1股东池的加权分红；
  V1股东：除去最大社区市场，其它社区累计50ETH；
  全球2%业绩加权平均分红；
  V2股东：除去最大社区市场，其它社区累计100ETH；
  全球2%业绩加权平均分红；
  V3股东：除去最大社区市场，其它社区累计300ETH；
  全球2%业绩加权平均分红；
  V4股东：除去最大社区市场，其它社区累计500ETH；
  全球2%业绩加权平均分红；
 9、合约资金分配
  股东分红池：8%
  幸运池：4%
  平衡分红池：84%
  技术团队：4%`;